.stars {
  display: flex;
}
    
.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-image: url(/images/star.svg);
  background-position: 2px 2px;
  background-repeat: no-repeat;
}
    
.stars__star:nth-child(-n + 4) {
  background-image: url(/images/star-active.svg);
}
